* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    color: aliceblue;
}

.contact {
    position: relative;
    min-height: 100vh;

    padding: 50 100px;
    display: flex;
    justify-content: center;
    align-content: center;
    flex-direction: column;



}

.contact .content {
    margin-top: 4%;
    margin-left: 50%;
    transform: translate(-50%, -50%);
    max-width: 800px;
    text-align: center;
}

.contact .content h2 {
    font-size: 28px;
    font-weight: 600;
    color: #fff;
}

.contact .content p {

    font-weight: 400;
    color: #fff;

}

.container {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 40px;
    margin-right: 30px;
}

.container .contactInfo {
    width: 50%;
    display: flex;
    flex-direction: column;
    margin-top: 24%;
    position: relative;

    left: 5%;
    transform: translate(-50%, -50%);
}

.container .contactInfo .box {
    position: relative;
    padding: 20px 0;
    display: flex;
}

.container .contactInfo .box .icon {
    min-width: 50px;
    height: 50px;
    background: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    font-size: 22px;

}

.container .contactInfo .box .icon i {
    color: black;
}

.container .contactInfo .box .text {
    display: flex;
    margin-left: 20px;
    font-size: 15px;
    color: #fff;
    flex-direction: column;
    font-weight: 300;
    margin-right: 40px;

}

.pass {
    position: relative;
    left: 95px;
    top: 4px;
}



.container .contactInfo .box .text h3 {
    font-weight: 600;
    color: #00bcd4;
    font-size: 20px;
}

.container .contactInfo .box .text p {
    font-size: 18px;
    font-weight: 400;
}

.contactForm {
    width: 400px;
    height: 500px;

    margin-top: -20%;
    position: relative;
    left: 1000px;

    transform: translate(-50%, -50%);

}


.contactForm span {
    font-size: 18px;
    margin-bottom: 2px;
    float: left;
    padding-left: 5px;
}

.contactForm h2 {
    font-size: 20px;
    color: rgb(238, 229, 229);
    font-weight: 500;
    position: absolute;
    left: 0px;
}

.contactForm .input {
    position: relative;
    left: 90px;
    width: 100%;
    margin-top: 16px;


}

.typo {
    border: none;
    width: 100%;
    padding: 5px 0;
    font-size: 20px;
    margin: 10px 0;
    border-bottom: 2px solid rgb(236, 235, 233);
    background: none;
    outline: none;
}

.contactForm .inputBox .typo,
.contactForm .inputBox textarea {
    width: 100%;
    padding: 5px 0;
    font-size: 16x;
    margin: 10px 0;
    border: none;
    border-bottom: 2px solid #fff;
    outline: none;
    resize: none;
    color: rgb(231, 226, 226);
}

.contactForm .inputBox textarea {
    color: black;
    font-size: 16px;
}

.sub {
    width: 140px;
    height: 50px;
    border-radius: 5px;
    border: 2px solid rgb(56, 56, 55);
    background-color: rgb(218, 98, 98);
    font-weight: 800;
}

.sub:hover {
    animation: diffe 1s;
    animation-iteration-count: infinite;


}

@keyframes diffe {
    0% {
        background: linear-gradient(10deg, rgb(130, 224, 6), rgb(194, 228, 3));
        border: 2px solid rgb(42, 217, 248);
    }

    25% {
        background: linear-gradient(50deg, rgb(15, 231, 231), rgb(13, 240, 89));
        border: 2px solid rgb(197, 28, 28);
    }

    75% {
        background: linear-gradient(100deg, rgb(184, 11, 252), rgb(55, 6, 233));
        border: 2px solid wheat;
    }

    100% {
        background: linear-gradient(80deg, rgb(223, 8, 187), rgb(226, 6, 61));
        border: 2px solid lightpink;
    }
}

@media (max-width: 911px) {
    .contact {
        padding: 50px;
    }

    .content {
        align-items: center;
        position: absolute;
        left: -2%;
        top: -2%;

    }

    .container {
        display: flex;
        flex-direction: column;
    }

    .container .contactInfo {
        position: absolute;
        top: 90%;
        left: 52%;
        transform: translate(-50%, -50%);
    }

    .container .contactInfo {
        width: 100%;

    }

    .contactForm {
        width: 400px;
        height: 500px;

        margin-top: 60%;
        position: relative;
        left: 240px;

        transform: translate(-50%, -50%);

    }

}